
import {
    Options,
    Vue
} from 'vue-class-component';
import en from "@/languages/en.json";
import br from "@/languages/br.json";

@Options({
    data() {
        return {
            language: "en",
            text: {
                en,
                br,
            },
        };
    },
    computed: {
        languageChosen() {
            return (this.language = this.$store.getters.language);
        },
    },
})
export default class Portfolio extends Vue {
text: any;
languageChosen: any;
}
