import { createStore } from 'vuex'

export default createStore({
  state: {
    language: "en",
  },
  getters: {
    language: state => state.language,
  },
  mutations: {
    changeLanguage: (state, language) => {
      state.language = language ? "en" : "br"
    },
  },
  actions: {
  },
  modules: {
  }
})
